.btn-outer {
  /* width: 100px; */
  height: 100px;
  border: 3px solid black;
  border-radius: 13px;
  background: linear-gradient(to bottom left, #000000eb, #9e9e9e70);
  
}

.btn-inner {
  color: #fff;
  position: relative;
  display: grid;
  /* width: 80px; */
  place-items: center;
  margin: 8px;
  height: 80px;
  background: linear-gradient(180deg, #2f2f2f, #3f3f3f);
  box-shadow: inset -8px 0 8px rgba(0, 0, 0, 0.15),
    inset 0 -8px 8px rgba(0, 0, 0, 0.25), 0 0 0 2px rgba(0, 0, 0, 0.75),
    10px 20px 25px rgba(0, 0, 0, 0.4);
  user-select: none;
  cursor: pointer;
  font-weight: 400;
  border-radius: 10px;
}

.btn-inner:active {
  transform: scale(0.95, 0.95);
}

.outer-plus{
  height: 100%;
  box-sizing: border-box;
}

.inner-plus{
  height: 92%;
}

.outer-equal {
 
  background: linear-gradient(to bottom left, #47e6ffeb, #2096f4);
}

.inner-equal{
  background: linear-gradient(180deg, #2196f5, #2196f3);
}

.outer-clear{

  background: linear-gradient(to bottom left, #d92020eb, #ff000070);
}

.inner-clear{
  
  background: linear-gradient(
    180deg,#ff2424, #ff1400);
}
